import { seo, pages } from "projectSettings"
import React from "react"
import { GatsbySeo } from "gatsby-plugin-next-seo"
import Layout from "globalLayoutsIndexPage"
import styled from "styled-components"
import HomePageCover from "projectOrganisms/HomePageCover"
import openGraphImageUrl from "projectStatic/open-graph/default.jpg"

const PageStyle = styled.div`
  .title-wrapper {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    .experience-info {
      color: ${({ theme }) => theme.tint_orange};
    }
  }
  .my-way-ul {
    overflow: hidden;
    li.active {
      :before {
        background-color: ${({ theme }) => theme.tint_green};
      }
      span {
        color: ${({ theme }) => theme.tint_green};
      }
    }
  }
`

export default function IndexPage(props) {
  function calcExperience() {
    let num = 1992
    let now = new Date().getFullYear()
    let res = now - num - 16
    return res
  }
  return (
    <>
      <GatsbySeo
        title={pages.index.title}
        description={pages.index.description}
        canonical={seo.siteUrl}
        openGraph={{
          url: props.location.href,
          title: pages.index.title,
          description: pages.index.description,
          images: [
            {
              url: `${seo.siteUrl}${openGraphImageUrl}`,
              width: 1200,
              height: 630,
              alt: seo.siteSeoPrefix,
            },
          ],
        }}
      />
      <Layout>
        <section style={{ marginBottom: "128px" }}>
          <HomePageCover avatar>
            <span className="xxxLarge-high-title mb-16 fw-bold">
              Senior UX/UI Designer
            </span>
            <span className="xxxLarge-h1">Middle Web Developer</span>
          </HomePageCover>
        </section>
        <PageStyle
          as="article"
          className="container-fluid"
          style={{ maxWidth: "992px" }}
        >
          <div className="title-wrapper">
            <h1 className="mb-16">My way</h1>
            <span className="experience-info mb-16">
              {calcExperience()} years of experience
            </span>
          </div>
          <hr className="mt-0"></hr>

          <ul className="my-way-ul">
            <li>
              2008 - 2015 - Received education. Work on freelance exchanges.
              Cooperation with Ukrainian and Russian web studios.
            </li>
            <li>
              2015 - 2016 - Worked in a Belarusian web studio as Middle UX/UI
              Designer.
            </li>
            <li>
              <p>
                2016 - I decided to establish cooperation with regular customers
                and distance myself from freelance exchanges. To do this, I
                started pages on behance and dribble, and also started a
                personal blog.
              </p>
              <p>
                After six months, I managed to occupy almost all of my working
                time working with regular customers.
              </p>
              <p>
                Most of all, it was the freelance exchanges that helped me in
                this.
              </p>
              <p>
                At the end of 2016, I gave up running a personal blog, as my
                articles began to be unceremoniously stolen by publications such
                as Medium.
              </p>
              <p>I also refused to use all social networks except Linkedin.</p>
              <p>
                After weighing all the pros and cons, it became obvious that it
                is not economically profitable for me to attract customers
                through dribbble and behance. Customers from these social
                networks often wanted me to design for the sake of design. I
                specialized in conversion optimization and solving real business
                problems.
              </p>
            </li>
            <li>
              <p>
                2017 - In the first half of the year, I started developing my
                own design system. He also deepened his knowledge in the field
                of user behavior analysis and conversion optimization for
                e-commerce sites.
              </p>
              <p>
                In the second half of the year, the first release of my design
                system took place, which was called Sitis Design System, further
                (SDS). Over the next six months of using it, my income tripled.
                I will describe its SDS functionality:
              </p>
              <ul>
                <li>
                  Optimization of work on routine tasks (UI Kit with fully
                  customizable components for any project, automatic export of
                  all icons in the project, various presets, etc.). As a result,
                  the time spent working on UX/UI decreased by at least 400%
                  (!). The more complex the project was, the more optimization
                  was felt.
                </li>
                <li>
                  Automation of communication with developers. Now a repository
                  on GitHub was automatically created for each project. All the
                  project data and files were stored in it. No one else needed
                  to search in messengers for links to the design, source code,
                  files with project icons, etc. Everything was in one place and
                  available at any time.
                </li>
                <li>
                  Semi-automatic work with branding. Automatic generation of
                  guidelines based on identity.
                </li>
                <li>
                  A substantial simplification of the continued support of
                  projects. Customers and programmers could request design
                  changes in a few clicks. The history of changes was kept. As
                  soon as I finished working on the next edit or innovation, all
                  project participants received a notification by email.
                </li>
              </ul>
            </li>
            <li>
              2018 - Began to specialize in the most complex projects. Developed
              UX/UI for mapping services in Ukraine and Belarus. Perfectly
              mastered Google and Apple Guidlines. Added a lot of patterns to my
              design system for developing mobile app design. Continued to work
              for Upwork.
            </li>
            <li>
              2019 - Deepened my knowledge in Front-end development. I learned
              the Bottstrap framework, started learning Java Script, React and
              Node JS. I transferred my design system from Sketch to Figma,
              significantly expanded its capabilities and applications. Refused
              to work for Upwork in favor of working with regular clients.
            </li>
            <li>
              2020 - Continued to learn Java Script and its frameworks. I wrote
              a component library in React for my design system. Moved most of
              my projects to my own codebase. He continued to cooperate with
              regular customers.
            </li>
            <li className="active">
              <span>NOW</span> - Started learning Python.
            </li>
          </ul>
        </PageStyle>
      </Layout>
    </>
  )
}
